const ExternalCarouselV = ({ data, interval, defaultActiveIndex }) => {
  const images = data.imagesV.map(i => (
    <Carousel.Item>
      <Img fluid={i.childImageSharp.fluid} />
    </Carousel.Item>
  ))

  return (
    <Carousel
      controls={false}
      indicators={false}
      interval={interval}
      defaultActiveIndex={+defaultActiveIndex}
    >
      {images}
    </Carousel>
  )
}
